import { ContentBlockTypes } from "../enums"
import { ContentBlock, ListBlock, TableBlock, TextBlock } from "../types/content.types"

export const isTextBlock = (block: ContentBlock): block is TextBlock => {
  return (
    block.type === ContentBlockTypes.HEADING ||
    block.type === ContentBlockTypes.FOOTER ||
    block.type === ContentBlockTypes.HEADER ||
    block.type === undefined
  )
}

export const isTableBlock = (block: ContentBlock): block is TableBlock => {
  return block.type === ContentBlockTypes.TABLE
}

export const isListBlock = (block: ContentBlock): block is ListBlock => {
  return (
    block.type === ContentBlockTypes.FORM ||
    block.type === ContentBlockTypes.LIST_UNORDERED ||
    block.type === ContentBlockTypes.LIST_ORDERED
  )
}
